.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: row;
    color: white;
}

.list,
.form {
    border: 1px solid #ededed;
    margin: 20px 10px;
    overflow: scroll;
    height: calc(100vh - 41px);
}

.list {
    width: 70%;
}

.form {
    width: 30%;
}

.heading {
    background-color: #fff;
    color: #000;
    margin: 0;
    padding: 10px;
    position: sticky;
    top: 0;
}

.list-container,
.form-container {
    padding: 10px;
    height: 100%;
}

.row {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    padding: 10px 0;
}

.input-box {
    width: 70%;
}

.input-label {
    width: 30%;
    text-align: left;
}

.no-border {
    border: 0px;
}

.zero-state {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.details > div,
.header > div {
    width: 20%;
    margin: 0 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.header {
    background-color: rgba(255, 255, 255, 0.3);
}

.header .date {
    flex-direction: row;
}

.amount {
    /* text-align: right !important; */
}

button {
    padding: 5px 10px;
    margin: 0 5px;
    cursor: pointer;
    text-transform: uppercase;
}

.sub-text {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.5);
}
